import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";
import Footer from "../components/Footer";
import AppBar from "../components/AppBar";
import { Link } from "gatsby";

import CourseList from "../components/CourseList";

import ProfilBild from "../images/AVATAR2.jpg";

const About = () => {
  return (
    <React.Fragment>
      <CssBaseline />

      <main>
        <AppBar />
        <Container maxWidth="md">
          <Box my={4}>
            <Container maxWidth="xs">
              <img
                style={{
                  display: "block",
                  width: "100%",
                  border: "1px solid #010A26",
                  marginLeft: "auto",
                  marginRight: "auto",
                  marginBottom: "40px",
                  borderRadius: "50%",
                }}
                src={ProfilBild}
                alt="Profile"
              />
            </Container>
            <Typography
              component="h4"
              variant="h4"
              gutterBottom
              color="primary"
            >
              About
            </Typography>
            <Typography
              color="primary"
              paragraph
              gutterBottom
              style={{ marginBottom: "40px" }}
            >
              My name is Fredrik and I am a 24 year old student with a Bachelor
              of Science degree from Link√∂pings University. I am currently in my
              fourth year of working towards my{" "}
              <a
                rel="noopener noreferrer"
                target="_blank"
                href="https://liu.se/studieinfo/en/program/6cmen/4641#"
                style={{
                  color: "#F20544",
                  textDecoration: "inherit",
                }}
              >
                Master of Science degree in Media Technology and Engineering
              </a>
              . My main interests lies in the fields of computer graphics,
              machine learning, artificial intelligence and multicore
              programming. My past projects can be found under{" "}
              <Link
                to="/projects"
                style={{
                  color: "#F20544",
                  textDecoration: "inherit",
                }}
              >
                projects
              </Link>
              . This website is implemented in JavaScript with React and
              GatsbyJS. Feel free to contact me if you have any questions, my
              contact information can be found in the footer.
            </Typography>
            <Typography
              component="h4"
              variant="h4"
              gutterBottom
              color="primary"
            >
              Upcoming courses
            </Typography>
            <CourseList completed={false} />
            <Typography component="h4" variant="h4" paragraph color="primary">
              Completed courses
            </Typography>
            <CourseList completed={true} />
          </Box>
        </Container>
        <Footer />
      </main>
    </React.Fragment>
  );
};

export default About;
