const CourseList = (props) => {
  if (props.completed) {
    return (
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <List dense={true} style={{ marginBottom: "50px" }}>
            {coursesCompletedLeft.map((course) => (
              <ListItem>
                <ListItemText primary={course.code} secondary={course.name} />
              </ListItem>
            ))}
          </List>
        </Grid>
        <Grid item xs={12} md={6}>
          <List dense={true} style={{ marginBottom: "50px" }}>
            {coursesCompletedRight.map((course) => (
              <ListItem>
                <ListItemText primary={course.code} secondary={course.name} />
              </ListItem>
            ))}
          </List>
        </Grid>
      </Grid>
    );
  } else {
    return (
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <List dense={true} style={{ marginBottom: "50px" }}>
            {coursesUpcomingLeft.map((course) => (
              <ListItem>
                <ListItemText primary={course.code} secondary={course.name} />
              </ListItem>
            ))}
          </List>
        </Grid>
        <Grid item xs={12} md={6}>
          <List dense={true} style={{ marginBottom: "50px" }}>
            {coursesUpcomingRight.map((course) => (
              <ListItem>
                <ListItemText primary={course.code} secondary={course.name} />
              </ListItem>
            ))}
          </List>
        </Grid>
      </Grid>
    );
  }
};

export default CourseList;
